.root {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    height: 100%;
    border: 1px solid transparent;
    border-radius: 3px;
    margin: 3px;
    padding: 3px;
}

.nonZero {
    background-color: #9ABD97;
    border: 1px solid #D0F1BF;
    border-radius: 3px;
}

.movable {
    background-color: #646536;
}

.movable:hover {
    cursor: pointer;
}


